const get = require('browser-get'),
      msg = require('./msg'),
      org = window.location.search.replace("?", "").split("&")[0].replace(/([^a-z0-9]+)/gi, ''),
      request = get('https://api.github.com/orgs/' + org),
      status = `${num} ${org} repos hearted.`;

request.then(function(resp){
  msg(resp);
});

request.catch(function(resp){
  msg('Invalid GitHub organization');
});
